@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap");

@media (min-width: 768px) {
  .contact-us {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: #89d0ca;
    height: 120vh;
  }

  .contact-us-img img {
    width: 40vw;
    height: 90vh;
  }

  .contact-us-img {
    margin-left: 5vw;
    margin-top: 30vh;
    position: absolute;
  }
}

.contact-us-form {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  width: 40vw;
  height: 80vh;
  margin-top: 20vh;
  padding-left: 10vw;
  margin-left: 40vw;
  padding-right: 5vw;

  h3,
  input,
  textarea {
    margin-top: 5vh;
    border-width: 0 0 2px;
    font-size: 1.5em;
    border-color: black;
    font-family: Oswald, sans-serif;
  }
  button {
    margin: 5vh auto;
    font-size: 1.5em;
    font-family: Oswald, sans-serif;
    border-color: black;
  }
  h3 {
    text-align: center;
  }
}
@media (max-width: 768px) {
  .contact-us {
    display: flex;
    flex-direction: column-reverse;
    background-color: #89d0ca;
  }

  .contact-us-img img {
    width: 50vw;
    height: 60vh;
    margin-left: 25vw;
  }

  .contact-us-form {
    width: 50vw;
    margin-left: 15vw;
    margin-top: 2vh;
    margin-bottom: 2vh;
  }
  .button {
    font-size: 1.5em;
    box-sizing: border-box;
  }
}
